/* NsrResult.css */
.player-container {
  text-align: center;
  margin: 10px;
  font-size: 20px;
}

.player-container td {
  border: 1px solid #ababab;
  padding-top: 35px;
  padding-bottom: 35px;
  font-size: 20px;
}

.player-container th {
  border: 1px solid #ababab;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.player-link {
  color: black;
  text-decoration: none; /* Remove underlines from links */
}

.player-link:hover {
  text-decoration: underline; /* Add underline on hover */
}

/* Additional styling for the table */

/* Center the content inside table cells */
td {
  vertical-align: middle;
}

span {
  margin-left: 5%; /* Set left margin to 10% */
}
