/* Login.css */

.login-container {
  max-width: 300px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  margin: 0 auto; /* Center-align the container */
  margin-top: 100px; /* Adjust the top margin as needed */
}

label {
  display: block;
  margin: 10px 0;
}

input {
  width: 100%;
  height: 80px;
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 15px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #4caf50;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 30px;
}

button:hover {
  background-color: #45a049;
}

/* Add media queries for responsiveness */
@media (max-width: 768px) {
  .login-container {
    /* Add responsive styles as needed */
  }
}

h2 {
  text-align: center;
}
